.outcome-details::before, .outcome-details::after {
  content: ' ';
  display: table; }

.outcome-details::after {
  clear: both; }

.outcome-details {
  list-style-type: none;
  margin-top: 0;
  padding: 0; }

* {
  box-sizing: border-box;
  font-family: inherit; }

html,
body {
  height: 100%; }

body {
  font-family: "Source Sans Pro", sans serif; }

p,
figure {
  margin: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1rem;
  margin: 0; }

.outcome {
  min-width: 24rem;
  padding: 2em; }
  .outcome-container {
    display: flex;
    align-items: center;
    height: 100%; }
  .outcome-header {
    position: relative;
    text-align: center;
    width: 12rem;
    height: 12rem;
    margin: 0 auto 1rem;
    padding: 3.875em 0 1em; }
  .outcome-figure {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .outcome-figure, .outcome-vector {
    width: 100%;
    height: 100%; }
  .outcome-circle {
    r: 47.5%;
    cx: 50%;
    cy: 50%;
    fill: transparent;
    stroke-width: 5%;
    stroke-dasharray: 37.68rem;
    transition: stroke-dashoffset 0.35s; }
  .outcome-rect:nth-of-type(1), .outcome-rect:nth-of-type(4) {
    x: 6rem;
    width: 0.15rem;
    height: 0.25rem; }
  .outcome-rect:nth-of-type(2), .outcome-rect:nth-of-type(3) {
    y: 6rem;
    width: 0.25rem;
    height: 0.15rem; }
  .outcome-rect:nth-of-type(1) {
    y: 0.725rem; }
  .outcome-rect:nth-of-type(2) {
    x: 11.025rem; }
  .outcome-rect:nth-of-type(4) {
    y: 11.025rem; }
  .outcome-rect:nth-of-type(3) {
    x: 0.725rem; }
  .outcome-title {
    text-transform: uppercase;
    color: #a2a2a2;
    font-size: 1.15em;
    font-weight: 600; }
  .outcome-value {
    font-size: 1.5em;
    font-weight: 600; }
  .outcome-details {
    border-bottom: 2px solid #a2a2a2; }
  .outcome-item {
    display: inline-block;
    float: left;
    width: 50%;
    padding-bottom: 0.85em; }
    .outcome-item:last-of-type {
      text-align: right; }
    .outcome-item-name {
      margin-bottom: 0.5em; }
    .outcome-item-price {
      color: #a2a2a2; }
    .outcome-item-percentage {
      margin-right: 0.5rem; }

.outcome-revenue-tablet-text {
  color: #59D21E; }

.outcome-revenue-tablet-circle {
  stroke: #59D21E; }

.outcome-revenue-mobile-text {
  color: #036600; }

.outcome-revenue-mobile-circle {
  stroke: #036600; }

.outcome-revenue-rect {
  fill: #2e9c0f; }

.outcome-impression-tablet-text {
