.outcome-impression-tablet-circle {
  stroke: #06C8E6; }

.outcome-impression-mobile-text {
  color: #004F60; }

.outcome-impression-mobile-circle {
  stroke: #004F60; }

.outcome-impression-rect {
  fill: #038ca3; }

.outcome-visits-tablet-text {
  color: #FDC100; }

.outcome-visits-tablet-circle {
  stroke: #FDC100; }

.outcome-visits-mobile-text {
  color: #D55214; }

