	}

	&-impression {
		@include outcome-themify($outcome-color-impressions-tablet, $outcome-color-impressions-mobile);
	}

	&-visits {
		@include outcome-themify($outcome-color-visits-tablet, $outcome-color-visits-mobile);
	}
}